	background-color: rgb(255,255,255);
}
.base-bac{
	 	position: relative;
		width: 100%;
		height: 0.8rem;
		border-radius: 0 0 19.5% 19.5%;
	    background:-webkit-gradient(linear,left top, left bottom,from(rgb(6,181,131)),to(rgb(62,191,160)));
	    background:linear-gradient(rgb(6,181,131),rgb(62,191,160));
}
.base-card,.personal-doc,.personal-his,.personal-life,.family-his,.personal-hab{
	border-radius: 0.1rem;
	-webkit-box-shadow: 0rem 0.035rem 0.135rem 0.02rem rgba(181,181,181,0.16);
	box-shadow: 0rem 0.035rem 0.135rem 0.02rem rgba(181,181,181,0.16);
	background-color: #fff;
}
.base-card{
	position: relative;
	width: 92%;
	height: 2rem;
	margin: -0.6rem 0.15rem 0rem 0.15rem;
	z-index: 3;
}
.base-pic{
	position: absolute;
	width: 45.98%;
	height: 1.57rem;
	margin: 0.22rem 0.155rem 0.21rem 1.755rem;
}
.base-pic img{
	width: 98%;
	height: 1.57rem;
}
.base-card .content .one ul li{
 	position: relative;
	float: left;
	height: 0.29rem;
	line-height: 0.29rem;
}
.base-card .content .one{
	position: absolute;
	margin-top: 0.25rem;
	margin-left: 0.25rem;
}
.base-card .content .one ul li .user{
	display: inline-block;
	position: absolute;
	white-space: nowrap;
	font-size: 0.22rem;
	font-weight: bold;
}
.base-card .content .one .box1{
 	position: relative;
	width: 0.45rem;
	height: 0.16rem;
	line-height: 0.15rem;
	margin-left: 0.8rem;
	margin-top: 0.09rem;
	text-align: center;
	background-color: rgb(127,160 ,248);
	border-radius: 0.075rem;
}
.base-card .content .one .box1 .box1-1{
	width: 0.5rem;
	height: 0.16rem;
}
.base-card .content .one .box1 .box1-1 img{
	position: absolute;
	display: block;
	width: 0.1rem;
	height: 0.1rem;
	padding-left: 0.05rem;
	padding-top: 0.032rem;
	z-index: 2;
}
.base-card .content .one .box1 .box1-1 label{
 	position: absolute;
 	width: 0.35rem;
	font-size: 0.1rem;
	color: rgb(255,255,255);	
	display: inline-block;
	margin-top:0.01rem;
	margin-bottom: 0.01rem;
	margin-left: -0.14rem;
}
.base-card .content .one .box2{
	width: 0.55rem;
	height: 0.2rem;
	color: rgb(62,191,160);
	border-radius: 0.105rem;
	background:rgb(245,255,245);
	border: 1px solid rgb(49,188,152);
	margin-top: 0.29rem;
	margin-left: 1.09rem;
	font-size: 0.14rem;
	padding:0.025rem 0.07rem 0.03rem 0.07rem;
}
.base-card .content .two{
 	width: 62%;
 	height: 0.96rem;
	padding:0.71rem 1.02rem 0.32rem 0.25rem;
}
.base-card .content .two ul li{
	display: block;
	width: 100%;
	line-height: 0.13rem;
	margin-top: 0.2rem;
	font-size: 0.13rem;
	color: rgb(48,48,48);
}
.base-card .content .two ul li.ethnic{
	margin-top: 0.04rem;
}
.base-card .content .two ul li.ethnic img, .base-card .content .two ul li.birth img, .base-card .content .two ul li.blood img, .base-card .content .two ul li.card img{
	position: relative;
	width: 0.15rem;
	height: 0.13rem;
	vertical-align: middle;
}
.base-card .content .two ul li.ethnic label, .base-card .content .two ul li.birth label, .base-card .content .two ul li.blood label, .base-card .content .two ul li.card label{
	display: inline-block;
	padding-left: 0.18rem;
	line-height: 0.13rem;
}
.base-card .content .two ul li.ethnic span, .base-card .content .two ul li.blood span{
		padding-left: 0.91rem;
		color: rgb(137,137,137);
		white-space: nowrap;
}
.base-card .content .two ul li.birth span, .base-card .content .two ul li.card span{
		display: inline-block;
		width: 0.2rem;
		padding-left: 0.64rem;
		color: rgb(137,137,137);
}
